nav {
  height: 5%;
}

a {
  color: white;
  text-decoration: none;
  font-size: 105%;
}

.intro-container {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
  width: 100%;
  height: 100vh;
}

.intro-logo {
  width: 40%;
  height: auto;
}

.navbar {
  display: grid;
  grid-template-columns: 50vw 50vw;
  overflow: visible;
}

.top-image {
  width: 100%;
  height: auto;  
}

.nav-logo{
  width: 50%;
}

.logo {
  width: 8vw;
  height: auto;
  margin-left: 2vw;
  margin-top: 1vw;
  z-index: 1;
}

.background-scratches {
  width: 100%;
  height: 100%;
  opacity: 0.15;
  position: fixed;
  z-index: -1;
}

.about-container {
  display: grid;
  grid-template-columns: 20% 40% 40%;
  -ms-flex-align: center;
      align-items: center;
}

.misty-container {
  grid-area: 1/1/2/4;
  -ms-flex-item-align: start;
      align-self: start;
  width: 100%;
}

.misty {
  width: 100%;
  margin-top: -15%;
}

.clever-words {
  grid-area: 1/2/2/3;
  -ms-flex-item-align: start;
      align-self: start;
  margin-top: 10%;
  z-index: 1;
  background-color: rgba(189, 190, 195, 0.5);
  -webkit-box-shadow: 0 0 50px 50px rgba(189, 190, 195, 0.5);
          box-shadow: 0 0 50px 50px rgba(189, 190, 195, 0.5);
}

.words {
  font-size: 1.25em;
  color: #171717;
}

@media only screen and (max-width: 1090px) {
  .clever-words {
    grid-area: 2/1/2/4;
    margin: 10%;
  }
}

.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0, 0.9);
  overflow-x: hidden;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.icon-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.icons {
  color: #818181;
  margin: 1vw;
  display: inline;
}

.icons:hover {
  color: #f8f8f8;
}

.overlay a:hover, .overlay a:focus {
  color: #f8f8f8;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}

@media screen and (max-height: 450px) {
.overlay a {font-size: 20px}
.overlay .closebtn {
  font-size: 40px;
  top: 15px;
  right: 35px;
}
}

.hamburger {
  font-size:30px;
  justify-self: end;
  cursor: pointer;
  color: rgba(23, 23, 23, 0.7);
  z-index: 2;
  margin-right: 5%;
}

#myNav {
  z-index: 2;
}

.material-icons {
  margin-top: 10%;
  font-size: 36px;
}

.home-container {
  display: grid;
  grid-template-columns: 25vw 50vw 25vw;
  margin-top: 20vh;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
}

.greeting {
  grid-column-start: 2;
  -ms-flex-item-align: center;
      align-self: center;
}

.hi{
  text-align: center;
  color: rgba(23, 23, 23, 0.7);
  font-size: 40px;
  text-shadow: 0px -1px 1px rgba(129, 129, 129, 0.7);
}

.name {
  color: #0b6623;
  text-shadow: 1px 0px 1px rgba(129, 129, 129, 0.7);
}

.description {
  text-align: center;
  color: rgba(23, 23, 23, 0.7);
}

.project-container {
  display: grid;
  grid-template-columns: 10vw 80vw 10vw;
  justify-items: center;
}

.project-display {
  grid-column-start: 2;
}

.project-head {
  display: inline-block;
  margin-left: 10%;
  line-height: 0;
}

.project-title {
  display: inline-block;
  color: rgba(23, 23, 23, 0.7);
}

.line-break {
  border: 2px solid #0b6623;
  background-color: #0b6623
}

.project-flex-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
}

.project {
  width: 400px;
  height: 250px;
  margin: 1vw;
  border-radius: 2%;
  -ms-flex-line-pack: center;
      align-content: center;
  overflow: hidden;
  position: relative;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}


.project-shot {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  -webkit-transition: .7s ease;
  -o-transition: .7s ease;
  transition: .7s ease;
}

.project-overlay {
  background-color: rgba(23, 23, 23, 0.7);
  position: absolute;
  display: grid;
  grid-template-columns: 10% 80% 10%;
  grid-template-rows: 15% 25% 60%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: .7s ease;
  -o-transition: .7s ease;
  transition: .7s ease;
}

.project-overlay2 {
  background-color: rgba(23, 23, 23, 0.8);
  position: absolute;
  display: grid;
  grid-template-columns: 10% 80% 10%;
  grid-template-rows: 16% 24% 60%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: .7s ease;
  -o-transition: .7s ease;
  transition: .7s ease;
}

.project-tech-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  grid-column: 2/ span 1;
  grid-row: 2/ span 1;
}

.project-tech {
  color: white;
  background-color: red;
  margin: 4%;
  padding: 5px;
  background-color: rgba(129, 129, 129, 1);
  border-radius: 10px;
  -webkit-box-shadow: -1px 1px#171717;
          box-shadow: -1px 1px#171717;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.project-link {
  text-decoration: underline;
}

.project-link-list {
  grid-column: 2/ span 1;
  grid-row: 3/ span 1;
  justify-self: center;
  -ms-flex-item-align: end;
      align-self: end;
  color: white;
  text-align: center;
  text-shadow: -1px 1px #171717;
  margin-bottom: 10%;
}

.project:hover .project-overlay{
  opacity: 1;
}

.project:hover .project-overlay2{
  opacity: 1;
}

.project:hover .project-shot {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05)
}




